body {
  background-color: #e5e5e5;
  margin: 0;
  font-family: Roboto, sans-serif;
}

.card {
  box-sizing: border-box;
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  margin: 16px 19px;
}

img {
  width: 160px;
  margin: 32px auto 0;
  display: block;
}

.card__details {
  margin: 40px 16px 0;
}

.card__details-title {
  color: #060b35;
  margin: 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__details-code {
  color: #616070;
  margin: 0;
  padding-top: 5px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  margin: 0 16px;
  display: flex;
}

.card__price-text {
  color: #616070;
  margin: 0;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__price-number {
  color: #060b35;
  margin: 0;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button a {
  color: #fff;
  text-transform: uppercase;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
}

.card__button a:hover {
  color: #00acdc;
}

.card__button {
  background-color: #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 166px;
  height: 40px;
  margin: 16px;
  display: inline-flex;
}

.card__button:hover {
  background-color: #fff;
  border: 1px solid #00acdc;
}

.card__button:hover a {
  color: #00acdc;
}

.card__stars {
  justify-content: space-between;
  margin: 10px 16px 12px;
  display: flex;
}

.stars {
  flex-wrap: wrap;
  align-content: center;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  flex-wrap: wrap;
  width: 16px;
  height: 16px;
  margin-right: 4px;
  display: flex;
}

.stars--1 .stars__star:nth-child(-n+1), .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.card__stars-review {
  color: #060b35;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

